import React, { useState } from "react";

export const ModalClient = (props) => {
  const { setVisible, setTaskSource } = props;
  const [source, setSource] = useState("");

  const handleChange = (event, sourceName) => {
    const { value } = event.target;

    if (value === "on") {
      setSource(sourceName);
    } else {
      setSource(null);
    }
  };

  const handleClick = () => {
    setTaskSource(source);
    setVisible(false);
  };

  return (
    <div class="modal">
      <div class="modal-add-wrapper">
        <div class="modal-container">
          <button class="close" onClick={() => setVisible(false)}></button>
          <div class="modal-inner-content">
            <div class="modal-title">
              <span>Добавить контакт</span>
            </div>
            <div class="modal-row">
              <div class="modal-label">
                <span>Поиск</span>
              </div>
              <div class="modal-input">
                <input type="text" />
              </div>
            </div>
            <div class="modal-row">
              <div class="staff-items">
                <div class="staff-item">
                  <div class="staff-container">
                    <div class="staff-inner-items">
                      <div class="staff-inner-content">
                        <div class="staff-name">
                          <span>Андрей Пономаренко</span>
                        </div>
                      </div>
                      <div class="staff-inner-checkbox">
                        <label class="radio">
                          <input
                            class="radio-input"
                            type="radio"
                            onChange={(e) => handleChange(e, "Mustafo Faiz")}
                          />
                          <div class="radio-button"></div>
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-button">
              <button class="button" onClick={handleClick}>
                <span>Добавить</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
