import React, { useState } from "react";

import { ModalClient } from "../ModalClient";
import { TaskDate } from "./components/TaskDate";
import { TaskSource } from "./components/TaskSource";
import { TaskOwners } from "../TaskOwners";

import "./styles.css";

export const TaskPopupAdd = (props) => {
  const { setClose } = props;

  const [visible, setVisible] = useState(false);
  const [taskDescription, setTaskDescription] = useState("");
  const [taskSource, setTaskSource] = useState(null);
  const [taskSourceType, setTaskSourceType] = useState(null);
  const [date, setDate] = useState(null);

  const handleClick = (action, type) => {
    if (action === "delete") {
      setTaskSource(null);
      setTaskSourceType(null);
    } else {
      setVisible(true);
    }
  };

  return !visible ? (
    <div class="modal">
      <div class="side-container">
        <button class="close" onClick={() => setClose(true)}></button>
        <div class="side-content">
          <div class="side-header">
            <div class="side-headline">
              <div class="side-title">
                <span>Новая задача</span>
              </div>
            </div>
          </div>
          <div class="side-body">
            <div class="side-row">
              <div class="side-form">
                <div class="side-string">
                  <div class="side-label">
                    <span>Описание задачи</span>
                  </div>
                  <div class="side-input">
                    <textarea
                      placeholder="Позвонить клиенту"
                      onChange={(e) => setTaskDescription(e.target.value)}
                    >
                      {taskDescription}
                    </textarea>
                  </div>
                </div>

                <TaskSource
                  taskSource={taskSource}
                  taskSourceType={taskSourceType}
                  handleClick={handleClick}
                ></TaskSource>
                <TaskDate date={date} setDate={setDate}></TaskDate>
                <TaskOwners></TaskOwners>
              </div>
            </div>
          </div>
          <div class="side-footer">
            <div class="side-row">
              <div class="side-button">
                <button class="button">
                  <span>Создать задачу</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  ) : (
    <ModalClient
      setVisible={setVisible}
      setTaskSource={setTaskSource}
    ></ModalClient>
  );
};
