import React, { useState } from "react";

import image from "@images/no-avatar.png";
import * as classnames from "classnames";

export const TaskItem = (props) => {
  const { name, source, count } = props;
  const [checked, setChecked] = useState(props.checked);

  const Creators = Array.from({ length: +count }, () => {
    return (
      <a
        href="#"
        class="task-creator-item tooltip-hint-bottom"
        data-hint="Сергей Ярославцев"
      >
        <div class="task-creator-avatar">
          <img src={image} />
        </div>
      </a>
    );
  });

  const handleChange = () => {
    setChecked(!checked);
  };

  const classNames = classnames("tast-list-item", { "task-checked": checked });

  return (
    <div class={classNames}>
      <div class="task-body">
        <label class="checkbox">
          <input
            class="checkbox-input"
            type="checkbox"
            onChange={handleChange}
          />
          <div class="checkbox-button"></div>
        </label>
        <div class="task-name">
          <a href="#">
            <span>{name}</span>
          </a>
        </div>
        <div class="task-source">
          <a href="#">
            <span>{source} </span>
          </a>
        </div>
        <div class="task-creator">
          <div class="task-creator-items">{Creators}</div>
        </div>
      </div>
    </div>
  );
};
