import React, { useState } from "react";
import { Link } from "react-router-dom";
import { inject } from "mobx-react";

import { AuthInput } from "@shared/AuthInput";
import image from "@images/logo-auth.svg";

export const RecoveryPassword = inject("authStore")((props) => {
  const { changePassword } = props.authStore;
  const [email, setEmail] = useState("");

  const handleClick = () => {
    changePassword({ email });
  };

  const handleChange = (event, fieldType) => {
    const { value } = event.target;

    switch (fieldType) {
      case "email":
        setEmail(value);
        break;
      default:
        return;
    }
  };

  return (
    <div className="auth">
      <div className="auth-modal">
        <div className="auth-logo">
          <img src={image} />
        </div>
        <div className="auth-container">
          <div className="auth-title">
            <span>Восстановление пароля</span>
          </div>
          <div className="auth-row">
            <AuthInput
              label="E-mail"
              handleChange={handleChange}
              fieldType="email"
            ></AuthInput>
          </div>
          <div className="auth-row">
            <div className="auth-string">
              <div className="auth-button">
                <button className="button" onClick={handleClick}>
                  <span>Восстановить</span>
                </button>
              </div>
            </div>
          </div>
        </div>
        <div className="auth-footer">
          <div className="auth-items">
            <div className="auth-item">
              <Link to="/login" className="auth-link">
                <span>Войти</span>
              </Link>
            </div>
            <div className="auth-item">
              <Link to="/registration" className="auth-link">
                <span>Регистрация</span>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
