import React, { useState } from "react";

import { Deal } from "@components/Deal";
import { DealStage } from "@components/DealStage";
import { ContentHeader } from "@components/ContentHeader";
import { DealStageAdd } from "./component/DealStageAdd";
import { DealTableEdit } from "./component/DealTableEdit";

import { SelectricWrap } from "@hoc/SelectricWrap";

import "./styles.css";

export const Deals = SelectricWrap((props) => {
  const { children } = props;

  const [addStage, setAddStage] = useState(false);
  const [editDealTable, setEditDealTable] = useState(false);

  const handleClose = (type) => {
    switch (type) {
      case "addStage":
        setAddStage(false);
        break;
      case "editTable":
        setEditDealTable(false);
        break;
      default:
        break;
    }
  };

  const options = {
    title: "Сделки",
    optionValues: [
      ["Основная воронка"],
      ["Действующие сделки", "Архив сделок"],
    ],
    buttonText: "Добавить воронку",
    handleClick: () => {
      setEditDealTable(true);
    },
  };

  return (
    <>
      {children}
      <div className="content">
        <div className="content-inner">
          <ContentHeader {...options}></ContentHeader>
          <div className="content-row">
            <div className="kanban">
              {editDealTable && (
                <DealTableEdit handleClose={handleClose}></DealTableEdit>
              )}
              {addStage && (
                <DealStageAdd handleClose={handleClose}></DealStageAdd>
              )}
              <div className="kanban-step">
                <div className="kanban-step-items">
                  <DealStage
                    title="Первый контакт"
                    count="0"
                    color="#e64848"
                  ></DealStage>
                  <DealStage
                    title="Переговоры"
                    count="0"
                    color="#48aae6"
                  ></DealStage>
                  <DealStage
                    title="В работе"
                    count="0"
                    color="#fb3232"
                  ></DealStage>
                  <DealStage
                    title="Согласование"
                    count="0"
                    color="#e68b48"
                  ></DealStage>
                  <DealStage
                    title="Завершение"
                    count="0"
                    color="#8e48e6"
                  ></DealStage>
                  <div className="kanban-step-item">
                    <div className="kanban-step-add-button">
                      <div
                        className="kanban-step-add-button-title"
                        onClick={() => setAddStage(true)}
                      >
                        <span>Добавить этап</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="kanban-group">
                <div className="kanban-group-items">
                  <Deal></Deal>
                  <Deal></Deal>
                  <Deal></Deal>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
});
