import React from "react";

import { Select } from "@components/Select";

export const ContentHeader = (props) => {
  const { title, optionValues, buttonText, handleClick } = props;

  const Selects = optionValues.map((optionValue) => (
    <Select optionValues={optionValue}></Select>
  ));

  return (
    <div class="content-header">
      <div class="panel">
        <div class="panel-items">
          <div class="panel-item">
            <div class="panel-title">
              <span>{title}</span>
            </div>
          </div>
          <div class="panel-item">
            <div class="panel-button-items" onClick={handleClick}>
              <div class="panel-button-item">
                <a href="#" class="button">
                  <span>{buttonText}</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="filter">
        <div class="filter-select">{Selects}</div>
        <div class="filter-input">
          <div class="filter-input-search">
            <input type="text" placeholder="Поиск..." />
          </div>
        </div>
      </div>
    </div>
  );
};
