import { action, makeObservable, observable } from "mobx";

import { StatusCode } from "@utils/enums";

const endpoint1 = "http://localhost:8000/users/insert";
const endpoint2 = "http://localhost:8000/users/complete_auth/";
const endpoint3 = "http://localhost:8000/users/login";
const endpoint4 = "http://localhost:8000/users/change_pass";

export class AuthStore {
  constructor() {
    this.user = {
      authenticated: false,
    };

    this.completeRegistration = this.completeRegistration.bind(this);

    makeObservable(this, {
      user: observable,
      completeRegistration: action,
    });
  }

  async registrateUser(data) {
    try {
      const response = await fetch(endpoint1, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });

      const { status, id } = await response.json();

      if (status === StatusCode.OK) {
        return id;
      } else {
        throw new Error("Something went wrong!");
      }
    } catch (e) {
      throw e;
    }
  }

  async completeRegistration(id, data) {
    try {
      const response = await fetch(endpoint2 + id, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });

      const { token, status } = await response.json();
      console.log(token, status, this);

      if (status === StatusCode.OK) {
        this.user.authenticated = true;
        localStorage.setItem("token", token);
      }

      return "success";
    } catch (e) {
      console.log(e);
      throw e;
    }
  }

  async authenticateUser(data) {
    try {
      const response = await fetch(endpoint3, {
        method: "POST",
        body: JSON.stringify(data),
      });

      const { token, status } = await response.json();

      if (status === StatusCode.OK) {
        localStorage.setItem("token", token);
        this.user.authenticated = true;
      }
    } catch (e) {
      throw e;
    }
  }

  changePassword(data) {
    fetch(endpoint4, {
      method: "POST",
      body: JSON.stringify(data),
    });
  }
}
