import React, { useState } from "react";
import * as classnames from "classnames";

import { colors } from "./__mock__/data";

export const DealStageAdd = (props) => {
  const { handleClose } = props;
  const [stageName, setStageName] = useState("");
  const [stageColor, setStageColor] = useState("");
  const [activeIndex, setActiveIndex] = useState(0);

  const Colors = colors.map((color, index) => {
    const classNames = classnames("modal-color", {
      active: index === activeIndex,
    });

    return (
      <button
        class={classNames}
        style={{ backgroundColor: color }}
        onClick={() => setActiveIndex(index)}
      ></button>
    );
  });

  return (
    <div class="modal">
      <div class="modal-add-wrapper">
        <div class="modal-container">
          <button
            class="close"
            onClick={() => handleClose("addStage")}
          ></button>
          <div class="modal-inner-content">
            <div class="modal-title">
              <span>Добавить этап</span>
            </div>
            <div class="modal-row">
              <div class="modal-label">
                <span>Наименование</span>
              </div>
              <div class="modal-input">
                <input
                  type="text"
                  onChange={(e) => setStageName(e.target.value)}
                />
              </div>
            </div>
            <div class="modal-row">
              <div class="modal-color-list">{Colors}</div>
            </div>
            <div class="modal-button">
              <button class="button">
                <span>Добавить</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
