import React, { useEffect, useState } from "react";
import { inject } from "mobx-react";
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";

import { AuthInput } from "@shared/AuthInput";
import logoImage from "@images/logo-auth.svg";

const fieldData = [
  {
    label: "E-mail",
    fieldType: "email",
  },
  {
    label: "Пароль",
    filedType: "password",
  },
];

export const Registration = inject("authStore")((props) => {
  const history = useHistory();

  const { registrateUser } = props.authStore;
  const [error, setError] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  useEffect(() => {
    const id = localStorage.getItem("id");

    if (id) {
      history.push("/registration-complete");
    }
  });

  const handleClick = async () => {
    try {
      const id = await registrateUser({ email, password });
      localStorage.setItem("id", id);
      history.push("/registration-complete");
    } catch (e) {
      setError(true);
    }
  };

  const handleChange = (event, fieldType) => {
    const { value } = event.target;

    switch (fieldType) {
      case "email":
        setEmail(value);
        break;
      case "password":
        setPassword(value);
        break;
      default:
        return;
    }
  };

  const AuthInputs = fieldData.map(({ label, fieldType }) => (
    <AuthInput
      label={label}
      fieldType={fieldType}
      handleChange={handleChange}
      important={true}
    ></AuthInput>
  ));

  return (
    <div className="auth">
      <div className="auth-modal">
        <div className="auth-logo">
          <img src={logoImage} />
        </div>
        <div className="auth-container">
          <div className="auth-title">
            <span>Регистрация</span>
          </div>
          <div className="auth-row">{AuthInputs}</div>
          <div className="auth-row">
            <div className="auth-string">
              <div className="auth-button">
                <button className="button" onClick={handleClick}>
                  <span>Регистрация</span>
                </button>
              </div>
            </div>
          </div>
          {error && (
            <div className="auth-row">
              <div className="auth-string">
                <div className="auth-error">
                  <span>Неверный пароль</span>
                </div>
              </div>
            </div>
          )}
        </div>
        <div className="auth-footer">
          <div className="auth-items">
            <div className="auth-item">
              <Link to="/login" className="auth-link">
                <span>Войти</span>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
