import React, { useEffect } from "react";
import { Header } from "../components/Header";

import * as $ from "jquery";
import "selectric";

export const SelectricWrap = (Component) => (props) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  useEffect(() => {
    $("select").selectric();
  }, []);
  return (
    <Component {...props}>
      <Header></Header>
    </Component>
  );
};
