import React, { useState } from "react";
import { Link } from "react-router-dom";
import { inject } from "mobx-react";

import image from "@images/logo-auth.svg";
import { AuthInput } from "@shared/AuthInput";

const fieldData = [
  {
    label: "E-mail",
    fieldType: "email",
  },
  {
    label: "Пароль",
    filedType: "password",
  },
];

export const Login = inject("authStore")((props) => {
  const { authenticateUser } = props.authStore;

  const [error, setError] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleClick = async () => {
    try {
      await authenticateUser({ email, password });
    } catch (e) {
      setError(true);
    }
  };

  const handleChange = (event, fieldType) => {
    const { value } = event.target;
    switch (fieldType) {
      case "email":
        setEmail(value);
        break;
      case "password":
        setPassword(value);
        break;
      default:
        break;
    }
  };

  const AuthInputs = fieldData.map(({ label, fieldType }) => (
    <AuthInput
      label={label}
      fieldType={fieldType}
      handleChange={handleChange}
      important={true}
    ></AuthInput>
  ));

  return (
    <div className="auth">
      <div className="auth-modal">
        <div className="auth-logo">
          <img src={image} />
        </div>
        <div className="auth-container">
          <div className="auth-title">
            <span>Войти</span>
          </div>
          <div className="auth-row">{AuthInputs}</div>
          <div className="auth-row">
            <div className="auth-string">
              <div className="auth-button">
                <button className="button" onClick={handleClick}>
                  <span>Войти</span>
                </button>
              </div>
            </div>
          </div>
          {error && (
            <div className="auth-row">
              <div className="auth-string">
                <div className="auth-error">
                  <span>Неверный пароль</span>
                </div>
              </div>
            </div>
          )}
        </div>
        <div class="auth-footer">
          <div class="auth-items">
            <div class="auth-item">
              <Link to="/password-recovery" class="auth-link">
                <span>Забыли пароль?</span>
              </Link>
            </div>
            <div class="auth-item">
              <Link to="/registration" class="auth-link">
                <span>Регистрация</span>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
