import React, { useEffect } from "react";
import image from "@images/no-avatar.png";

import * as $ from "jquery";

export const Header = () => {
  useEffect(() => {
    $(".header-account-avatar").click(function () {
      $(".header-account-dropdown").toggleClass("active");
    });

    $(document).on("click", function (e) {
      if (!$(e.target).closest(".header-account-avatar").length) {
        $(".header-account-dropdown").removeClass("active");
      }
      e.stopPropagation();
    });
  });

  return (
    <div className="header">
      <div className="header-items">
        <div className="header-item">
          <div className="main-header-title">
            <span>Название моей компании</span>
          </div>
        </div>
        <div className="header-item">
          <div className="header-inner-items">
            <div className="header-inner-item">
              <div className="header-navigation">
                <div className="header-navigation-items">
                  <a href="#" className="header-navigation-item">
                    <span>Живая лента</span>
                  </a>
                  <a href="#" className="header-navigation-item">
                    <span>Сотрудники</span>
                  </a>
                  <a href="#" className="header-navigation-item">
                    <span>Тарифы</span>
                  </a>
                </div>
              </div>
            </div>
            <div className="header-inner-item">
              <div className="header-account">
                <div className="header-account-avatar">
                  <img src={image} />
                </div>
                <div className="header-account-dropdown">
                  <div className="header-account-dropdown-container">
                    <div className="header-account-dropdown-items">
                      <a href="#" className="header-account-dropdown-item">
                        <span>Профиль</span>
                      </a>
                      <a href="#" className="header-account-dropdown-item">
                        <span>Настройки</span>
                      </a>
                      <a href="#" className="header-account-dropdown-item">
                        <span>Выход</span>
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
